<template>
  <h1 class="ml5">
    <span class="text-wrapper">
      <span class="line line1"></span>
      <span class="letters letters-left">Happy</span>
      <span class="letters ampersand"> 8 </span>
      <span class="letters letters-right">Months</span>
      <span class="line line2"></span>
    </span>
  </h1>
</template>

<script>
import anime from 'animejs'

export default {
  mounted () {
    anime.timeline({
      loop: false,
    })
    .add({
      targets: '.ml5 .line',
      opacity: [0.5,1],
      scaleX: [0, 1],
      easing: "easeInOutExpo",
      duration: 700,
      delay: 9000,
    }).add({
      targets: '.ml5 .line',
      duration: 600,
      easing: "easeOutExpo",
      translateY: function(e, i, l) {
        var offset = -0.625 + 0.625*2*i;
        return offset + "em";
      }
    }).add({
      targets: '.ml5 .ampersand',
      opacity: [0,1],
      scaleY: [0.5, 1],
      easing: "easeOutExpo",
      duration: 600,
      offset: '-=600'
    }).add({
      targets: '.ml5 .letters-left',
      opacity: [0,1],
      translateX: ["0.5em", 0],
      easing: "easeOutExpo",
      duration: 600,
      offset: '-=300'
    }).add({
      targets: '.ml5 .letters-right',
      opacity: [0,1],
      translateX: ["-0.5em", 0],
      easing: "easeOutExpo",
      duration: 600,
      offset: '-=600'
    })
  }  
}
</script>


